.app-header {
  background: linear-gradient(90deg, #4f46e5, #6366f1);
  color: white;
  padding: 2rem 1rem;
  text-align: center;
  border-radius: 0 0 0.5rem 0.5rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}
.app-header h1 {
  margin: 0;
  font-size: 2rem;
  letter-spacing: 0.05em;
}
